<template lang="html">
  <div class="">
    <default-head header="拆分福包"></default-head>
    <div id="wrapper" class="page-cnt-box">
      <div class="">
        <div class="text-box" >
          <flm-form-input label="福包爱心总额" textAlign="right" color="#fc4d54"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="拆分福包数量" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input label="剩余爱心数量" textAlign="right"></flm-form-input>
        </div>
        <div class="text-box" >
          <flm-form-input type="password" label="支付密码" placeholder="请输入支付密码" textAlign="right"></flm-form-input>
        </div>
      </div>
      <div class="package-split-notice">
        <p>备注: 当前爱心可拆分成4个福包, 拆分剩余的爱心自动转入天使爱心</p>
      </div>
      <sub-button class="area-foot-btn" text="确定拆分" marginTop="1rem"></sub-button>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import subButton from '@/components/common/subButton'
export default {
  components: { defaultHead, subButton }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.page-cnt-box {
  .text-box {
    background: @whiteColor;
    padding: 0 .22rem;
    border-bottom: 1px solid @lineColor;
  }
  .text-box:last-child {
    border-bottom: none
  }
}
.package-split-notice {
  margin-top: 0.2rem;
  height: .88rem;
  line-height: .5rem;
  font-size: .3rem;
  padding: 0 .22rem;
  color: #999;
}
</style>



